#entryBox {
    position: relative;
    width: 80%;
    height: 5em;
    overflow-x: hidden;
    display: flex;
    align-items: center;

    #dateBox {
        position: absolute;
        background-color: white;
        box-shadow: 0px 0px 10px lightgrey;
        width: 4em;
        height: 4em;
        letter-spacing: normal;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 5px;
        border-radius: 20px;
        left: 4%;
        top: 10%;
        z-index: 2;

        #date {
            font-size: 2em;
        }

        #day {
            left: 0;
            color: red;
        }
    }

    #value {
        position: absolute;
        width: 100%;
        top: 25%;
        background-color: lightblue;
        display: flex;
        justify-content: flex-start;
        z-index: 1;
        border-radius: 5px;

        #view_month {
            margin-left: 2.8em;
            color: white;
            font-size: 2em !important;
            letter-spacing: normal;
            font-size: larger;
        }
    }

    #displayEntry {
        position: absolute;
        right: 2%;
        top: 0;
        width: 8em;
        height: 100%;
        overflow: hidden;
        text-decoration: none;
        color: grey;
        letter-spacing: normal;
        z-index: 2;
        display: flex;
        align-items: center;
    }

    .linkClass {
        color: black;
    }
}

.evenDateBoxIndex {
    left: auto !important;
    right: 4% !important;
}

.evenMonthIndex {
    justify-content: flex-end !important;

    #view_month {
        margin-right: 2.8em;
    }
}

.evenDisplayIndex {
    left: 5%;
}

@media (max-width: 360px) {
    #displayEntry {
        display: none;
    }
}

@media (min-width: 700px) {
    #view_month {
        margin-left: 4em !important;
    }

    .evenMonthIndex {
        #view_month {
            margin-right: 4em !important;
        }
    }
}